import {AfterContentInit, AfterViewInit, Component, ContentChild, forwardRef, Host, Input, OnInit} from '@angular/core';
import {ProjectionLike} from 'ol/proj';
import {UrlFunction} from 'ol/Tile';
import stylefunction from 'ol-mapbox-style/stylefunction';
import {applyStyle, applyBackground} from 'ol-mapbox-style';
import {SourceComponent} from './source.component';
import {LayerVectorTileComponent} from '../layers/layervectortile.component';
import {MapComponent} from '../map.component';
import {SourceVectorTileComponent} from './vectortile.component';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'aol-source-mapbox',
  template: `
    <ng-content></ng-content>
  `,
  providers: [{provide: SourceComponent, useExisting: forwardRef(() => SourceMapboxVectorTileComponent)}],
})
export class SourceMapboxVectorTileComponent extends SourceVectorTileComponent implements AfterContentInit {

  @Input() styleUrl: string;

  @Input() sourceName: string;

  @Input() spriteJsonUrl: string;

  @Input() spriteUrl: string;

  private styleLoaded = false;

  private spriteJsonLoaded = false;

  private glStyle = {};

  private spriteData = {};

  constructor(@Host() layer: LayerVectorTileComponent, private map: MapComponent, private http: HttpClient) {
    super(layer);
  }


  ngAfterContentInit(): void {
    super.ngAfterContentInit();

    this.http.get(this.styleUrl).subscribe(styleObject => {
      this.glStyle = styleObject;
      this.styleLoaded = true;
      this.applyMapboxStyle();
    });

    this.http.get(this.spriteJsonUrl).subscribe(spriteData => {
      this.spriteData = spriteData;
      this.spriteJsonLoaded = true;
      this.applyMapboxStyle();
    });
  }


  applyMapboxStyle() {
    // set the mapbox style
    if (this.spriteJsonLoaded && this.styleLoaded) {
      stylefunction(this.host.instance, this.glStyle, this.sourceName, undefined, this.spriteData, this.spriteUrl);
      applyBackground(this.map.instance, this.glStyle);
    }
  }

}
